<template>
  <div class="verify-wrap">
    <app-header title="核销券" showBack="true"></app-header>
    <div class="verify-main ios-s">
      <div class="title">用户信息</div>
      <mt-field label="Vin号" placeholder="请输入Vin号后八位" v-model="cardInfo.vin"></mt-field>
      <a href="javascript:void(0)" class="mint-cell mint-field">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title"><span class="mint-cell-title">类别</span></div>
          <div class="mint-cell-value">
            <mt-radio
            title="请选择核销类别"
            v-model="cardInfo.types"
            :options="['服务', '购物']">
          </mt-radio>
          </div>
        </div>
      </a>
      <mt-field v-show="showJobNumber" label="工单号" placeholder="请输入工单号" v-model="cardInfo.job_number"></mt-field>
      <div id="token-wrap">
        <mt-field
            label="使用码" v-for="(item, index) in cardInfo.tokens"
            :key="index"
            v-model="item.num"
            placeholder="请输入您的使用码" id="token">
              <i class="fa fa-minus" aria-hidden="true" style="font-size: 1.5rem;" @click="minusInput"></i>
              <i class="fa fa-plus" aria-hidden="true" style="font-size:1.5rem;" @click="clickPulsBtnHandler"></i>
        </mt-field>
      </div>
      <mt-field label="购券人手机号" placeholder="请输入手机号" v-model="cardInfo.buyCardTel"></mt-field>
      <mt-field label="购车人手机号" placeholder="请输入手机号" v-model="cardInfo.buyCarTel"></mt-field>
      <div class="clearfix img-wrap" id="imgWrap" style="padding:0 1rem;">
        <p>请上传发票</p>
        <div class="uploader-box" style="width: 3rem; height: 3rem;">
          <input type="file" ref="resource" id="file" v-on:change="uploadImg">
        </div>
      </div>
      <mt-button @click="submitInfo" type="primary" size="large" style="margin: 2rem auto;">确认</mt-button>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import AppHeader from '../../components/header/header'
import {verificationOneCard, uploadFaPiao} from '../../api/maintain'
export default {
  name: 'Dealer-Hexiao',
  data () {
    return {
      cardInfo: {
        vin: '',
        buyCardTel: '',
        buyCarTel: '',
        imgs: '',
        types: '',
        job_number: '',
        tokens: [
          {num: ''}
        ],
        num: 1
      },
      showJobNumber: false
    }
  },
  mounted () {},
  watch: {
    'cardInfo.types' (val) {
      if (val === '服务') {
        this.showJobNumber = true
      } else {
        this.showJobNumber = false
      }
    }
  },
  methods: {
    uploadImg () {
      let formData = new FormData()
      formData.append('0', this.$refs.resource.files[0])

      uploadFaPiao(formData)
        .then(data => {
          let url = data.data.url
          this.cardInfo.imgs = url
          let img = document.createElement('img')
          let imgWrap = document.getElementById('imgWrap')

          img.style.width = '144px'
          img.setAttribute('src', url)
          imgWrap.appendChild(img)

          this.$toast('发票上传成功')
        })
        .catch(err => {
          console.error(err)
          this.$toast('服务器繁忙, 请重试')
        })
    },
    clickPulsBtnHandler () {
      this.cardInfo.tokens.push({num: ''})
    },
    minusInput () {
      if (this.cardInfo.tokens.length > 1) {
        this.cardInfo.tokens.splice(this.cardInfo.tokens.length -1, 1)
      }
    },
    submitInfo () {
      if (!this.cardInfo.types) return this.$toast('必须选择核销类别')
      if (this.cardInfo.types === '服务' && !this.cardInfo.job_number) return this.$toast('必须填写工单号')
      let type = this.selectTypes(this.cardInfo.types)
      let tokens = []
      this.cardInfo.tokens.forEach((item) => {
        tokens.push(item.num)
      })
      verificationOneCard(tokens.toString(), this.cardInfo.vin, this.cardInfo.imgs, this.cardInfo.buyCardTel, this.cardInfo.buyCarTel, this.cardInfo.job_number, type)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg)
            this.$router.back()
          } else {
            this.$toast(data.errormsg)
          }
        })
        .catch(err => {
          this.$toast('网络错误:' + err.data.errormsg || err.message)
        })
    },
    selectTypes (type) {
      switch (type) {
        case '服务':
          return type = 1
          break
        case '购物':
          return type = 2
          break
      }
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
.verify-wrap {
  background: #e5e5e5;
  height: 100%;
  padding-bottom: 55px;
  overflow-x: hidden;
  overflow-y: scroll;
  .verify-main {
    margin-top: 40px;
    .title{
      padding: .6rem;
      color: #999;
    }
    .img-wrap{
      background: #fff;
    }
  }
}
</style>



